import React, { useState } from 'react';
import { MdClose, MdMenu } from 'react-icons/md';
import { useTransition, animated, config } from 'react-spring';
import { Link } from 'react-router-dom';

import avatar from '../../assets/avatar.jpg';
import { ContMenu } from './styles';

const styleToggleButton = {
  fontSize: '3rem',
  color: 'red',
  padding: 0,
  border: 'none',
  background: 'none',
  borderBottomRightRadius: '8px',
};

const Menu = () => {
  const [isOpen, setIsOpen] = useState(false);

  const fullscreenMenu = useTransition(isOpen, null, {
    from: {
      opacity: 0,
      transform: 'scale(0.80)',
    },
    enter: {
      opacity: 1,
      transform: 'scale(1)',
      background: 'rgba(0, 0, 0,  .8)',
      position: 'absolute',
      maxHeight: '70vh',
      maxWidth: '130vh',
      top: 0,
      bottom: 0,
      right: 0,
      left: 0,
      borderBottomRightRadius: '20px',
    },
    leave: { opacity: 0, transform: 'scale(0.80)' },
    config: config.gentle,
  });

  const openButton = useTransition(isOpen, null, {
    from: {
      opacity: 0,
      transform: 'scale(0)',
      position: 'absolute',
    },
    enter: {
      opacity: 1,
      transform: 'scale(1)',
    },
    leave: { opacity: 0, transform: 'scale(0)' },
    config: config.stiff,
  });

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div
      style={{
        padding: '1rem',
        overflow: isOpen ? 'hidden' : 'auto',
        maxHeight: '150vh',
        border: '3px',
      }}
    >
      <div
        style={{
          position: 'relative',
          zIndex: 20,
        }}
      >
        {openButton.map(({ item, key, props }) =>
          !item ? (
            <animated.div key={key} style={props}>
              <button style={styleToggleButton} onClick={toggleMenu}>
                <MdMenu />
              </button>
            </animated.div>
          ) : (
            <animated.div key={key} style={props}>
              <button style={styleToggleButton} onClick={toggleMenu}>
                <MdClose />
              </button>
            </animated.div>
          )
        )}
      </div>
      <ContMenu>
        <div style={{ paddingTop: '50px' }}>
          {fullscreenMenu.map(
            ({ item, key, props }) =>
              item && (
                <animated.div key={key} style={props}>
                  <ul>
                    <li>
                      <Link to="/portfolio">Home</Link>
                      <Link to="/contact">Contact</Link>
                      <Link to="/about">About</Link>
                    </li>
                    <li className="avatar">
                      <img src={avatar} alt="avatar" />
                      <span>Henrique Hoinacki</span>
                    </li>
                  </ul>
                </animated.div>
              )
          )}
        </div>
      </ContMenu>
    </div>
  );
};

export default Menu;
