import styled from 'styled-components';

export const Container = styled.div`
  div {
    display: flex;
    justify-content: center;
  }

  ul {
    display: flex;
    flex-direction: row;
    justify-content: center;

    position: fixed;
    bottom: 5rem;

    align-items: center;
  }

  li {
    display: flex;
    align-items: center;

    list-style-type: none;
    font-weight: bold;
    font-size: 3rem;
    margin-right: 2rem;
    color: #ccc6ce;

    svg {
      margin-right: 1rem;
    }
  }

  a {
    background: none;
    transition: 0.7s;

    display: table;
    font-weight: bold;
    text-align: center;

    color: #ccc6ce;
    text-decoration: none;
    transition: 0.7s;
  }

  a:hover {
    color: #7bf021;
  }
`;
