import styled from 'styled-components';

export const ContMenu = styled.div`
  ul {
    display: grid;

    grid-template-columns: 2fr 4fr;

    height: 100%;

    justify-content: left;
    font-size: 3vw;
    padding: 4rem;
  }

  li {
    margin-top: 10rem;

    display: flex;
    flex-direction: column;
  }

  a {
    font-weight: bold;
    border-radius: 0.6rem;
    text-align: center;

    color: #ccc6ce;
    text-decoration: none;
    margin-top: 1.5rem;

    transition: 0.7s;

    align-items: center;
  }

  a:hover {
    background-color: #7bf021;
    color: currentColor;
  }

  .avatar {
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
  }

  img {
    height: 7rem;
    width: 7rem;

    border-radius: 50%;
  }

  span {
    font-size: 20px;
    font-weight: 700;
    color: #ccc6ce;
    margin-right: 1rem;
  }
`;
